import React from 'react';
import PropTypes from 'prop-types';

import './ContactForm.css'

function ContactForm({formError, formSuccess, formPending, name, email, website, phone, subject, message, handleSubmit, handleChange}) {
    return (
        <div className="form-container">
            <form id="contact" onSubmit={handleSubmit}>
                <h3>Quick Contact</h3>
                <h4>Contact us today, and get reply with in 24 hours!</h4>
                <fieldset>
                    <input
                        name={'name'}
                        value={name}
                        className={'form-control'}
                        placeholder="Your name" type="text" tabIndex="1" required autoFocus
                        onChange={handleChange}
                    />
                </fieldset>
                <fieldset>
                    <input
                        name={'email'}
                        value={email}
                        className={'form-control'}
                        placeholder="Your Email Address" type="email" tabIndex="2"
                        onChange={handleChange}
                    />
                </fieldset>
                <fieldset>
                    <input
                        name={'phone'}
                        value={phone}
                        className={'form-control'}
                        placeholder="Your Phone Number" type="tel" tabIndex="3"
                        onChange={handleChange}
                    />
                </fieldset>
                <fieldset>
                    <input
                        name={'website'}
                        value={website}
                        className={'form-control'}
                        placeholder="Your Web Site starts with http://" type="url" tabIndex="4"
                        onChange={handleChange}
                    />
                </fieldset>
                <fieldset>
                    <input
                        name={'subject'}
                        value={subject}
                        className={'form-control'}
                        placeholder={'Subject'}
                        onChange={handleChange}
                    />
                </fieldset>
                <fieldset>
                    <textarea
                        name={'message'}
                        value={message}
                        className={'form-control'}
                        placeholder="Type your Message Here...." tabIndex="5">

                    </textarea>
                </fieldset>
                <fieldset>
                    <div className="g-recaptcha" data-sitekey={process.env.REACT_APP_RECAPTCHA_KEY}></div>
                </fieldset>


                {formSuccess && (
                    <div className={'d-flex justify-content-center align-items-center'}>
                        <div className={'alert alert-success alert-dismissible'}>
                            <b>Success</b>
                        </div>
                    </div>
                )}
                {formError && (
                    <div className={'d-flex justify-content-center align-items-center'}>
                        <div className={'alert alert-danger alert-dismissible'} style={{width: '10rem'}}>
                            <b>Error!</b>
                        </div>
                    </div>
                )

                }

                {
                    formPending &&
                    <div className={'bouncing-loader d-flex justify-content-center align-items-center'}>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                }

                <fieldset>
                    <button
                        className={'btn btn-primary'}
                        name="submit"
                        type="submit" id="contact-submit" data-submit="...Sending">Submit
                    </button>
                </fieldset>


            </form>


        </div>
    );
}

ContactForm.propTypes = {};
ContactForm.defaultProps = {};

export default ContactForm;
